<template>
	<div>
		<div :class="{'bianhui' : yincang}">
		<Ddtop mes="确认订单"></Ddtop>
		<div class="fenge"></div>

		<!-- 组件订单详情 -->
		<Ddxinxi></Ddxinxi>

		<!-- 底端立即支付 -->
		<div class="gozhifu">
			<div class="zhifu" @click="lastzhifu()">
				去支付
			</div>
			<div class="jine">
				应付金额:<span>￥{{zhifujine}}</span>
			</div>
		</div>
	</div>

	<!-- 支付方式 -->
	<div class="zhifufs" v-show="yincang">
		<div class="fs-top">
			<div class="fs-out" @click="cha()">
				<img src="/img/fukuanfs/cha.png" alt="">
			</div>
			确认付款
		</div>
		<div class="jutije">
			￥{{zhifujine}}
		</div>
		<p>选择付款方式</p>
		<div class="zhifubao">
			<div>
				<div>
					<img src="/img/fukuanfs/zhifubao.png" alt="">
				</div>
				<div>
					支付宝付款
				</div>
				<input type="radio" name="fangshi" value="zfb" checked="checked">
			</div>
			<div>
				<div>
				<img src="/img/fukuanfs/weixin.png" alt="">
				</div>
				<div>
					微信付款
				</div>
				<input type="radio" name="fangshi" value="wx">
			</div>
		</div>
		<div class="goqueren" @click="goqueren()">
			确认付款
		</div>
	</div>
	</div>
</template>

<script>
	import Ddtop from "../../components/dingdan-top.vue"
	import Ddxinxi from "../../components/ddxinxi.vue"
export default {
	name: 'querendd',
	components: {
		Ddtop,
		Ddxinxi
	},
	data(){
		return{
			zhifujine:0,
			yincang: false
		}
	},
	created() {
		this.zhifujine = this.$store.state.heji+10
	},
	methods:{
		lastzhifu(){
			this.yincang = true
			//禁止页面滚动
			var mo=function(e){e.preventDefault()}
			document.body.style.overflow='hidden'
			document.addEventListener("touchmove",mo,false)
		},
		cha(){
			this.yincang = false
			//取消禁止
			var mo=function(e){e.preventDefault()}
			document.body.style.overflow=''
			document.addEventListener("touchmove",mo,false)
		},
		// 最终付款提交
		goqueren(){
			this.$router.push("/fksuccess")
			//取消禁止
			var mo=function(e){e.preventDefault()}
			document.body.style.overflow=''
			document.addEventListener("touchmove",mo,false)
		}
	}
}
</script>

<style scoped>
	.bianhui{
		opacity: 0.8;
		background: #bababa;
	}
	.fenge{
		height: 20px;
		width: 100%;
		background: #eeeeee;
	}

	.gozhifu{
		height: 100px;
		line-height: 100px;
		width: 100%;
		position: fixed;
		bottom: 0px;
		background: white;
		border-top: 1px solid #eeee;
	}
	.gozhifu>div{
		float: right;
	}
	.gozhifu .zhifu{
		width: 240px;
		color: white;
		background: #ff5510;
		text-align: center;
		font-size: 30px;
	}
	.gozhifu .jine{
		font-size: 20px;
		color: #1b1b1b;
		margin-right: 20px;
	}
	.gozhifu .jine>span{
		color: #ff5510;
		font-size: 24px
	}


	.zhifufs{
		height: 600px;
		width: 100%;
		position: fixed;
		bottom: 0px;
		background: white;
	}
	.zhifufs .fs-top{
		height: 80px;
		line-height: 80px;
		border-bottom: 1px solid #eeeeee;
		text-align: center;
		font-size: 36px;
		color: #1b1b1b;
		overflow: hidden;
	}
	.zhifufs .fs-top .fs-out{
		float: left;
		margin-left: 30px;
		margin-top: 5px;
	}
	.zhifufs .jutije{
		height: 130px;
		line-height: 130px;
		font-size: 60px;
		color: #1b1b1b;
		text-align: center;
	}
	.zhifufs>p{
		font-size: 24px;
		color: #515151;
		padding-left: 30px;
	}
	.zhifufs .zhifubao{
		height: 162px;
		padding-left: 30px;
		padding-right: 30px;
		width: 100%;
		margin-top: 30px;
	}
	.zhifufs .zhifubao>div{
		height: 80px;
		line-height: 80px;
		border-bottom: 1px solid #eeeeee;
		overflow: hidden;
		font-size: 24px;
		color: #1b1b1b;

	}
	.zhifufs .zhifubao>div>div{
		float: left;
	}
	.zhifufs .zhifubao>div>div:first-child{
		margin-top: 10px;
		margin-right: 20px;
	}
	.zhifufs .zhifubao>div>input{
		float: right;
		margin-top: 25px;
		height: 30px;
		width: 30px;
	}
	.zhifufs .goqueren{
		height: 80px;
		line-height: 80px;
		width: 400px;
		background: #84c225;
		margin: auto;
		margin-top: 60px;
		border-radius: 10px;
		font-size: 30px;
		color: white;
		text-align: center;
	}
</style>
